import React, { useEffect, useContext } from 'react'
import { withRouter } from "react-router"
import './App.scss'
import Routes from './routes'
import queryString from 'query-string'
import { IntegrationContext, IntegrationContextProvider } from 'contexts/IntegrationContext'

const App = () => {

  const integrationContext = useContext(IntegrationContext)
  const { token, backgroundColor, returnUrl } = queryString.parse(window.location.search)

  useEffect(() => {
    integrationContext.dispatch({
      type: "SET_DATA",
      payload: {
        token: token,
        backgroundColor: backgroundColor,
        returnUrl: returnUrl
      }
    })
  }, [])

  const render = () => {
    return (
      <div className="App">
        <Routes />
      </div>
    )
  }
  return render()
}

export default withRouter(IntegrationContextProvider(App))
