import React, { useReducer } from 'react'

const initialState = {
  token: "",
  backgroundColor: "FFFFFF",
  returnUrl: "google.com"
}

const reducer = (state, action) => {
  switch (action.type) {
    case 'SET_DATA':
      return {
        ...state,
        ...(action.payload || {})
      }
    default:
      throw new Error()
  }
}

export const IntegrationContext = React.createContext({})

export const IntegrationContextProvider = (Children) => {
  const Provider = props => {
    const [state, dispatch] = useReducer(reducer, initialState)

    return (
      <IntegrationContext.Provider value={{ ...state, dispatch }}>
        <Children {...props} />
      </IntegrationContext.Provider>
    )
  }
  return Provider
}
