import React from 'react'
import styles from './index.module.scss'

const SCInput = props => {
  const render = () => {
    const { title, type, placeholder, value, onChange } = props
    return (
      <div className={styles.scInput}>
        <span className="title">{title}</span>
        <input className="input" type={type} placeholder={placeholder} value={value} onChange={e => onChange(e.target.value)} />
        <div className="line" />
      </div>
    )
  }
  return render()
}

export default SCInput
