import { spring, presets } from 'react-motion'

export const getMotionValue = (value, config = presets.noWobble) => {
  return spring(value, { ...config })
}

export const quickValue = (value, wobbly = false) => {
  return getMotionValue(value, { stiffness: 150, damping: wobbly ? 12 : 30 })
}

export const normalValue = (value, wobbly = false) => {
  return getMotionValue(value, { stiffness: 120, damping: wobbly ? 12 : 30 })
}

export const slowValue = (value, wobbly = false) => {
  return getMotionValue(value, { stiffness: 110, damping: wobbly ? 12 : 30 })
}