import React, { useContext, useEffect, useRef } from 'react'
import styles from './index.module.scss'
import { PrescreenContext, PrescreenContextProvider } from 'contexts/PrescreenContext'
import { resetAudio, pauseAudio } from 'utils/webAudio'
import Splash from './Splash'
import ChooseEar from './ChooseEar'
import FirstEar from './FirstEar'
import TonePlay from './TonePlay'
import HowMany from './HowMany'
import NewTone from './NewTone'
import SecondEar from './SecondEar'

const PreScreen = props => {

  const prescreenContext = useContext(PrescreenContext)
  const refVolume = useRef()
  const refGate = useRef()

  const flowIndex = prescreenContext.flowIndex
  const earIndex = prescreenContext.earIndex
  const tonePlay = prescreenContext.tonePlay
  const oscillator = window.oscillator

  useEffect(() => {
    if (tonePlay) {
      setToneFlow()
      if (flowIndex === 3) {
        oscillator.start()
      }
    } else {
      stopTone()
      if (flowIndex === 13) {
        oscillator.stop()
      }
    }
  }, [tonePlay])

  const setToneFlow = () => {
    playTone(250)
    setTimeout(() => {
      stopTone()
    }, 1000)
    setTimeout(() => {
      playTone(1000)
    }, 2000)
    setTimeout(() => {
      stopTone()
    }, 3000)
    setTimeout(() => {
      playTone(2000)
    }, 4000)
  }

  const playTone = (frequency) => {
    if ([3, 9].includes(flowIndex)) {
      refVolume.current = 70
    } else if ([6, 12].includes(flowIndex)) {
      refVolume.current = 40
    }
    refGate.current = 1
    resetAudio(frequency, earIndex, (refVolume.current - 96), refGate.current)
  }

  const stopTone = () => {
    refVolume.current = 0
    refGate.current = 0
    pauseAudio()
  }

  const gotoNext = (ear = earIndex) => {
    if (flowIndex === 13) {
      props.history.push('/singer')
    } else {
      prescreenContext.dispatch({
        type: "SET_DATA",
        payload: {
          flowIndex: flowIndex + 1,
          earIndex: ear
        }
      })
    }
  }

  const render = () => {
    const _props = { gotoNext }
    return (
      <div className={styles.preScreen}>
        <div className="mobile-div">
          <Splash {..._props} />
          <ChooseEar {..._props} />
          <FirstEar {..._props} />
          <TonePlay {..._props} />
          <HowMany {..._props} />
          <NewTone {..._props} />
          <SecondEar {..._props} />
        </div>
      </div>
    )
  }

  return render()
}

export default PrescreenContextProvider(PreScreen)