import React, { useState, useEffect } from 'react'
import styles from './index.module.scss'
import classNames from 'classnames'
import { Motion } from 'react-motion'
import { getMotionValue, quickValue, normalValue, slowValue } from 'utils/animation'
import SCInput from 'components/SCInput'

const Email = props => {
  const { stepIndex, setStepIndex } = props

  const currentStepIndex = 1
  const initStyles = [
    { opacity: 0, marginLeft: 100, marginRight: -100 },
    { opacity: 0, marginLeft: 100, marginRight: -100 },
    { opacity: 0, marginRight: -100 }]
  const showStyles = [
    { opacity: getMotionValue(1), marginLeft: quickValue(0, true), marginRight: slowValue(0) },
    { opacity: getMotionValue(1), marginLeft: normalValue(0, true), marginRight: slowValue(0) },
    { opacity: getMotionValue(1), marginRight: slowValue(0, true) }]
  const passStyles = [
    { opacity: getMotionValue(0), marginTop: 0, marginLeft: quickValue(-100), marginRight: slowValue(100) },
    { opacity: getMotionValue(0), marginLeft: normalValue(-100), marginRight: slowValue(100) },
    { opacity: getMotionValue(0), marginRight: slowValue(100) }]
  const backStyles = [
    { opacity: getMotionValue(0), marginLeft: 100, marginRight: -100 },
    { opacity: getMotionValue(0), marginLeft: 100, marginRight: -100 },
    { opacity: getMotionValue(0), marginRight: -100 }]

  const [styleState, setStyleState] = useState(initStyles)
  const [showMessage, setShowMessage] = useState(false)
  const [selectedIndex, setSelectedIndex] = useState(1)
  const [email, setEmail] = useState("")

  useEffect(() => {
    let newStyles = passStyles
    if (stepIndex === currentStepIndex - 1) {
      newStyles = backStyles
    } else if (stepIndex === currentStepIndex) {
      newStyles = showStyles
    } else if (stepIndex === currentStepIndex + 1) {
      newStyles = passStyles
    }
    setStyleState(newStyles)
  }, [stepIndex])

  useEffect(() => {
    var reg = /^(\w)+(\.\w+)*@(\w)+((\.\w+)+)$/
    if (reg.test(email)) {
      setShowMessage(true)
    }
  }, [email])

  const render = () => {
    return (
      <div className={classNames(styles.email, { "hidden": stepIndex !== currentStepIndex })} >
        <div className="staggered" >
          <Motion defaultStyle={initStyles[0]} style={styleState[0]}>
            {interpolatingStyle =>
              <span className="title" style={interpolatingStyle}>What's your email?</span>
            }
          </Motion>
          <Motion defaultStyle={initStyles[1]} style={styleState[1]}>
            {interpolatingStyle =>
              <div className="inputs" style={interpolatingStyle}>
                <SCInput title="Email Address" type="text" placeholder="Email Address" value={email} onChange={setEmail} />
              </div>
            }
          </Motion>
          <div className={classNames("message-div", { "message-div-appear": showMessage })}>
            <span className="text">Would you like to receive free<br />Hearing Health information?</span>
            <div className="buttons">
              <div className="radio-button">
                <div className={classNames("radio", { "selected": selectedIndex === 0 })} onClick={() => {
                  setSelectedIndex(0)
                }} />
                <span className="text">Yes</span>
              </div>
              <div className="radio-button">
                <div className={classNames("radio", { "selected": selectedIndex === 1 })} onClick={() => {
                  setSelectedIndex(1)
                }} />
                <span className="text">No</span>
              </div>
            </div>
          </div>
          <Motion defaultStyle={initStyles[2]} style={styleState[2]}>
            {interpolatingStyle =>
              <div className="button" style={interpolatingStyle} onClick={() => {
                setStepIndex(currentStepIndex + 1)
              }} />
            }
          </Motion>
        </div>
      </div>
    )
  }
  return render()
}

export default Email
