import React, { useState, useEffect, useRef } from 'react'
import styles from './index.module.scss'
import classNames from 'classnames'
import lottie from 'lottie-web'
import SCButton from 'components/SCButton'

const notesItemNames = ["notes"]

const singerLottieTopFrame = 150
const singerItemNames = [
  "singer_arms",
  "singer_shoes",
  "singer_torso",
  "singer_head_below",
  "singer_head_above",

  // "singer_head_blink",
]

const toneLottieMaxFrame = 23
const toneLottieMinFrame = 7
const toneLottieCenterFrame = 15
const toneItemNames = [
  "singer_fine_tuning_below",
  "singer_fine_tuning_above",
  "singer_fine_tuning_tongue_text",
]

const sliderItemNames = [
  "droplet",
  "droplet_text",
]

// element dic
const elementDic = []
const allNames = singerItemNames.concat(sliderItemNames).concat(notesItemNames).concat(toneItemNames)
allNames.forEach((name) => {
  elementDic[name] = React.createRef()
})
// lottie dic
const lottieDic = []
allNames.forEach((name) => {
  lottieDic[name] = React.createRef()
})

const Singer = () => {

  const refElementArray = useRef(elementDic)
  const refLottieArray = useRef(lottieDic)
  const refSlider = useRef()
  const refSliderBaseY = useRef(0)
  const refToneBaseY = useRef(0)
  const refMouseDown = useRef(false)
  const refFromFrame = useRef(0)
  const refShowTone = useRef(false)

  const [volume, setVolume] = useState(0)
  const [goTone, setGoTone] = useState(false)
  const [showTone, setShowTone] = useState(false)

  useEffect(() => {
    singerItemNames.concat(sliderItemNames).forEach((name, index) => {
      const lottieAnimation = lottie.loadAnimation({
        container: refElementArray.current[name].current,
        renderer: 'svg',
        loop: false,
        autoplay: false,
        path: `animations/lottie/${name}.json`
      })
      lottieAnimation.addEventListener('DOMLoaded', () => {
        lottieAnimation.setSubframe(false)
        refLottieArray.current[name].current = lottieAnimation
        if (index === 0) {
          lottieAnimation.onComplete = () => {
            displayTone(lottieAnimation)
          }
          lottieAnimation.onEnterFrame = () => {
            backToSinger(lottieAnimation)
          }
        }
      })
    })

    notesItemNames.forEach(name => {
      const lottieAnimation = lottie.loadAnimation({
        container: refElementArray.current[name].current,
        renderer: 'svg',
        loop: true,
        autoplay: true,
        path: `animations/lottie/${name}.json`
      })
      lottieAnimation.addEventListener('DOMLoaded', () => {
        lottieAnimation.setSubframe(false)
        refLottieArray.current[name].current = lottieAnimation
      })
    })

    toneItemNames.forEach((name, index) => {
      const lottieAnimation = lottie.loadAnimation({
        container: refElementArray.current[name].current,
        renderer: 'svg',
        loop: false,
        autoplay: false,
        path: `animations/lottie/${name}.json`
      })
      lottieAnimation.addEventListener('DOMLoaded', () => {
        lottieAnimation.setSubframe(false)
        refLottieArray.current[name].current = lottieAnimation
        if (index === toneItemNames.length - 1) {
          lottieAnimation.onEnterFrame = () => {
            finishInit(lottieAnimation)
          }
        }
      })
    })

    document.addEventListener('mousemove', moveSlider, true)
    document.addEventListener('mouseup', stopMove, true)
    return () => {
      document.removeEventListener('mousemove', moveSlider, true)
      document.removeEventListener('mouseup', stopMove, true)
    }
  }, [])

  useEffect(() => {
    const maxVolume = refSlider.current.clientHeight
    let seekFrame = singerLottieTopFrame * (volume / maxVolume)
    refFromFrame.current = seekFrame
    singerItemNames.forEach(name => {
      const lottieAnimation = refLottieArray.current[name].current
      if (lottieAnimation) {
        lottieAnimation.goToAndStop(seekFrame, true)
      }
    })
  }, [volume])

  useEffect(() => {
    singerItemNames.forEach(name => {
      const lottieAnimation = refLottieArray.current[name].current
      if (lottieAnimation) {
        if (goTone) {
          lottie.setSpeed(2)
          lottieAnimation.goToAndPlay(singerLottieTopFrame, true)
        } else {
          lottie.setDirection(-1)
          lottieAnimation.play()
        }
      }
    })
  }, [goTone])

  const displayTone = (lottieAnimation) => {
    if (lottieAnimation.currentFrame > singerLottieTopFrame) {
      setShowTone(true)
      toneItemNames.forEach(name => {
        const lottieAnimation = refLottieArray.current[name].current
        lottieAnimation.stop()
        lottieAnimation.play()
      })
    }
  }

  const finishInit = (lottieAnimation) => {
    if (!refShowTone.current && lottieAnimation.currentFrame === toneLottieCenterFrame) {
      refShowTone.current = true
      toneItemNames.forEach(name => {
        const lottieAnimation = refLottieArray.current[name].current
        lottieAnimation.stop()
        lottieAnimation.goToAndStop(toneLottieCenterFrame, true)
      })
    }
  }

  const backToSinger = (lottieAnimation) => {
    if (refShowTone.current && lottieAnimation.currentFrame === singerLottieTopFrame + 1) {
      refShowTone.current = false
      refToneBaseY.current = 0
      singerItemNames.forEach(name => {
        const lottieAnimation = refLottieArray.current[name].current
        lottieAnimation.stop()
        lottie.setDirection(1)
        lottieAnimation.goToAndStop(refFromFrame.current, true)
        lottie.setSpeed(1)
      })
    }
  }

  const stopMove = () => {
    refMouseDown.current = false
  }

  const moveSlider = (e) => {
    // if (volume > 0) {
    //   const renderEls = refLottieArray.current["droplet_text"].current.renderer
    //   window.els = renderEls
    //   console.log('[Log]===', renderEls.elements[0], '===LOG_TEST_END')
    //   renderEls.elements[0].elements[0].updateDocumentData({ t: 'new text', s: 20 }, 0)
    // }

    if (refMouseDown.current) {
      const isTone = refShowTone.current
      const currentY = e.clientY

      let isFirstTime = false
      if (isTone) {
        if (refToneBaseY.current === 0) {
          refToneBaseY.current = currentY
          isFirstTime = true
        }
      } else {
        if (refSliderBaseY.current === 0) {
          refSliderBaseY.current = currentY
          isFirstTime = true
        }
      }

      if (!isFirstTime) {
        if (isTone) {
          let newValue = refToneBaseY.current - currentY
          const elementTone = refElementArray.current[toneItemNames[toneItemNames.length - 1]].current
          const maxValue = elementTone.clientHeight
          let seekFrame = toneLottieMaxFrame * (newValue / maxValue) + toneLottieCenterFrame
          if (seekFrame < 0) {
            seekFrame = 0
          } else if (seekFrame > toneLottieMaxFrame) {
            seekFrame = toneLottieMaxFrame
          }
          toneItemNames.forEach(name => {
            const lottieAnimation = refLottieArray.current[name].current
            lottieAnimation.goToAndStop(seekFrame, true)
          })
        } else {
          let newValue = refSliderBaseY.current - currentY
          const maxValue = refSlider.current.clientHeight
          if (newValue < toneLottieMinFrame) {
            newValue = toneLottieMinFrame
          } else if (newValue > maxValue) {
            newValue = maxValue
          }
          setVolume(newValue)
        }
      }
    }
  }

  const render = () => {
    return (
      <div className={styles.singer}>
        <div className="mobile-div">
          {
            notesItemNames.map((name, index) => {
              return (
                <div id={name} key={index} ref={refElementArray.current[name]} className="notes" hidden={showTone} />
              )
            })
          }
          {
            singerItemNames.map((name, index) => {
              return (
                <div id={name} key={index} ref={refElementArray.current[name]} className="singer" hidden={showTone} />
              )
            })
          }
          {
            toneItemNames.map((name, index) => {
              const refElement = refElementArray.current[name]
              if (index === toneItemNames.length - 1) {
                return (
                  <div id={name} key={index} ref={refElement}
                    className="tone" hidden={!showTone}
                    onMouseDown={() => {
                      refMouseDown.current = true
                    }} />
                )
              } else {
                return (
                  <div id={name} key={index} ref={refElement} className="tone" hidden={!showTone} />
                )
              }
            })
          }
          <div className="main-div">
            <div ref={refSlider} className={classNames("slider", { "transparent": goTone })}>
              <div className="top" />
              {
                sliderItemNames.map((name, index) => {
                  const refElement = refElementArray.current[name]
                  if (index === sliderItemNames.length - 1) {
                    return (
                      <div id={name} key={index} ref={refElement}
                        className="cursor" style={{ bottom: volume - 100 }}
                        onMouseDown={() => {
                          refMouseDown.current = true
                        }} />
                    )
                  } else {
                    return (
                      <div id={name} key={index} ref={refElement}
                        className="cursor" style={{ bottom: volume - 100 }} />
                    )
                  }
                })
              }
            </div>
            <div className={classNames("pop-up", { "hidden": goTone })}>
              <span className="text">Slide<span className="singer-text-bold">up</span>or<span className="singer-text-bold">down</span>to find the</span>
              <span className="text"><span className="hight-light">INIMUM</span>Volume Level at</span>
              <span className="text">which you can hear the tones</span>
            </div>
          </div>
          <span className={classNames("tone-text", { hidden: !goTone })}>Slide<span className="singer-text-bold">UP</span>or<span className="singer-text-bold">DOWN</span>to fine tune</span>
          <SCButton style={["fine-tune"]} title={goTone ? "Back" : "Fine Tone"} action={() => {
            if (!goTone) {
              setGoTone(true)
            } else {
              setShowTone(false)
              setGoTone(false)
            }
          }} />
          <SCButton style={["next"]} title="Next" action={() => {
            // props.history.push("/signup")
          }} />
        </div>
      </div>
    )
  }
  return render()
}

export default Singer
