import React, { useState, useContext, useEffect, useRef } from 'react'
import styles from './index.module.scss'
import classNames from 'classnames'
import { PrescreenContext } from 'contexts/PrescreenContext'
import videojs from 'video.js'
import 'video.js/dist/video-js.css'// must import its css
import LongSplash from 'static/videos/LongSplash.mp4'

const Splash = props => {

  const prescreenContext = useContext(PrescreenContext)
  const [splashEnd, setSplashEnd] = useState(false)
  const videoEl = useRef(null)

  const showSplash = prescreenContext.splash

  useEffect(() => {
    // addEventListener must be done before videojs init
    videoEl.current.addEventListener('timeupdate', videoEnd, false)
    videojs(videoEl.current, {
      controls: false,
      autoplay: true,
      preload: "auto",
      sources: [{
        src: LongSplash,
        type: "video/mp4"
      }]
    })
  }, [])

  useEffect(() => {
    let videoPlayer = videojs.getPlayer(videoEl.current)
    if (!showSplash && videoPlayer) {
      videoPlayer.pause()
      videoPlayer.dispose()
    }
  }, [showSplash])

  const videoEnd = () => {
    const video = videoEl.current
    if (video) {
      const currentTime = video.currentTime
      if (currentTime > 17) {
        setSplashEnd(true)
      }
      if (currentTime >= 22) {
        video.currentTime = 19
      }
    }
  }

  const render = () => {
    return (
      <div className={classNames(styles.splash, "frame", { [styles.invisible]: !showSplash })}>
        <video ref={videoEl} className="video-js vjs-video" />
        <div className="button" hidden={!splashEnd} onClick={() => {
          props.gotoNext()
        }} />
      </div>
    )
  }

  return render()
}

export default Splash
